.button {
  display: block;
  position: relative;
  border: 0;
  padding: 30px 40px;
  width: 100%;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
  color: $light;
  text-transform: uppercase;
  text-decoration: none;
  background-color: $accent;

  @media (max-width: $tablet-max-width) {
    padding: 19px 40px;
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
  }

  @media (max-width: $mobile-max-width) {
    width: 288px;
  }

  &--arrow {
    &::before {
      @include beforeafter;
      right: 40px;
      width: 15px;
      height: 15px;
      background-image: url("../img/arrow-desktop.png");
      background-repeat: no-repeat;
      background-position: center;
    }

    &:active::before {
      display: none;
    }
  }

  &:hover {
    background-color: $accent-hover;
  }
}
