.country {
  display: none;

  @media (max-width: $tablet-max-width) {
    display: none;
    flex-direction: column;
    text-align: center;
    padding-top: 33px;
  }

  @media (max-width: $mobile-max-width) {
    padding-top: 24px;
  }

  &--active {
    display: flex;

    @media (max-width: $tablet-max-width) {
      display: flex;
    }
  }
}

.country__info {
  width: calc(470 * 100% / 900);
  padding-right: calc(50 * 100% / 470);

  @media (max-width: $tablet-max-width) {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-right: 0;
    width: 100%;
  }
}

.country__feedback {
  width: calc(430 * 100% / 900);

  @media (max-width: $tablet-max-width) {
    width: 100%;
  }
}

.country__name {
  position: relative;
  padding-top: 10px;
  padding-bottom: 16px;
  font-weight: 600;
  font-size: 55px;
  line-height: 90px;
  letter-spacing: -1.245px;

  @media (max-width: $tablet-max-width) {
    margin-bottom: 20px;
    padding: 0;
    font-size: 30px;
    line-height: 150%;
    letter-spacing: normal;
  }

  @media (max-width: $mobile-max-width) {
    font-size: 24px;
  }

  &::before {
    @include headerunderline;
    width: 175px;
    height: 3px;
  }
}

.country__descr {
  margin-bottom: 50px;
  padding-top: 30px;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.466667px;

  @include fluid-font(1024px, 1920px, 15px, 16px);

  @media (max-width: $tablet-max-width) {
    margin-bottom: 20px;
    padding: 0;
    letter-spacing: normal;
  }
}

.country__purchase-button {
  width: 250px;
  padding: 30px 39px;

  @media (max-width: $tablet-max-width) {
    margin-bottom: 30px;
    padding: 19px 51px;
  }

  @media (max-width: $mobile-max-width) {
    margin-bottom: 49px;
    width: 288px;
  }
}
